import Vue from "vue";
export default {
  install(Vue, options) {
    //添加实例方法
    Vue.prototype.$loadShow = function(e){
      this.$store.commit("is_load",true);
    };
    Vue.prototype.$loadHide = function(e){
      this.$store.commit("is_load",false);
    };
  }
};



