<template>
  <div class="nav-top">
    <div class="logo"></div>
    <div class="nav-slide">
      <router-link @click="tabActive='index'"
                   to="/"
                   class="btn-audit"
                   :class="{on : $route.name !== 'PermissionManage'}"
      >玩家审核详情</router-link>
      <router-link @click="tabActive='manage'"
                   to="/permissionManage"
                   class="btn-manage"
                   :class="{on : $route.name === 'PermissionManage'}"
      >权限管理</router-link>
      <div class="nav-topR">
        <div class="userinfor">
          <div class="user-img"><img src="" alt="" /></div>
          <p class="user-name">{{uin}}</p>
        </div>
        <a href="javascript:;" class="btn-exit" @click="$loginOut()">退出</a>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "MyHeader",
      data(){
        return{
          tabActive:"index"
        }
      },
      computed: {
        uin(){
          return this.$cookies.get("uin")?this.$cookies.get("uin").replace(/^o[0]*/img,''):null;
        }
      },
      mounted(){
        if(!this.uin){
          location.href = "/static/report_judge/admin/index.html#/login"
        }
      },
      methods:{

      }
    }
</script>

<style scoped>

</style>



