<template>
  <div class="wrap" id="app">
    <h1 class="hidden">玩家审核详情</h1>
    <div class="content">
      <keep-alive>
      <router-view/>
      </keep-alive>
    </div>
    <my-load v-show="is_load"></my-load>
  </div>
</template>

<script>
import MyHeader from "./components/MyHeader";
import MyLoad from "./components/MyLoad";
import {mapState} from "vuex";
export default {
  name: 'App',
  components: {MyLoad, MyHeader},
  computed: mapState(["is_load"])
}
</script>

<style>

</style>



