<template>
  <div id="load">
    <div class="load-img">
      <img src="../assets/ossweb-img/loading.png"/>
    </div>
    <div class="load-txt">
      <p>数据正在加载中，请等待...</p>
    </div>
  </div>
</template>

<script>
    export default {
        name: "MyLoad"
    }
</script>

<style scoped>
  #load{
    background: #000;
    opacity: 0.8;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
  }
  .load-img{
    width: 62px;
    margin: 300px auto 0;
  }
  .load-img img{
    width: 62px;
    animation:rotate 1s linear infinite;
    -webkit-animation: rotate 1s linear infinite;
    -moz-animation: rotate 1s linear infinite;
    -ms-animation: rotate 1s linear infinite;
    -o-animation: rotate 1s linear infinite;
  }
  .load-txt {
    margin-top: 10px;
    color: #ddd;
  }
  .load-txt p{
    text-align: center;
    font-size: 20px;
  }
  @-webkit-keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @-moz-keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @-ms-keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
</style>



