import Vue from "vue";
export default {
  install(Vue, options) {
    //添加实例方法
    Vue.prototype.$timeFormat = function(date){
      let d = new Date(date);
      let year = d.getFullYear();
      let month = d.getMonth() + 1;
      let formatMath = month < 10 ? '0' + month : '' + month;
      let day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate();
      let hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours();
      let minute = d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes();
      let second = d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds();
      return `${year}-${formatMath}-${day} ${hour}:${minute}:${second}`;
    };
    /*Vue.prototype.$initEndTime = function () {
      this.$timeFormat(new Date().getTime());
    };
    Vue.prototype.$initStartTime = function () {
      this.$timeFormat(new Date().getTime()-7*24*60*60);
    }*/
  }
};



