import Vue from "vue";
export default {
  install(Vue, options) {
    //添加实例方法
    Vue.prototype.$op_type= {
      delete: 2,//2：取消(可恢复）
      //3：彻底删除
      add: 1,   //1：授权
      resume: 4,//4：恢复
    };
    Vue.prototype.$errTxt= {
      noData: "当前时间段内暂无对应数据，请调整筛选日期后重试",
      getDataErr: "数据请求失败，请尝试刷新页面，以重新获取数据",
      setAuthErr: "权限操作存在异常，请重试。",
      selectTimeErr: "结束时间要大于开始时间！请重新选择时间",
      noUser: "查询的用户不存在",
      detailErr: "详情获取失败，请重试",
      noAuth: "当前账号无权限访问页面，请与管理员确认",
      networkErr: "网络繁忙，请稍后重试",
      firstPage: "已经是第一页了！",
      lastPage: "已经是最后一页了！"
    };
    Vue.prototype.$code = {
      LOGINTIMEOUT: 17,//登录cookie失效
      NOAUTHORITY:{
        num: 10291351,
        txt: "账号取消权限失败，您无法对无权限账号进行取消操作"
      },
      INCORRECTFORMAT:{
        num:10291352,
        txt:"账号格式有误，请按照规定格式提供账号"
      },
      REPETEOPERATION:{
        num:10291353,
        txt:"账号授权失败，您无法对同一账号重复进行同一权限授权操作"
      },
      TOOLONGTIME:{
        num:10291354,
        txt:"选择的时间间隔不能超过3个月"
      },

    }
  }
};



