import Vue from "vue";
import pt from "../assets/pluginsJs/ptlogin_v1";
export default {
  install(Vue, options) {
    Vue.prototype.$showPt = function(){
      let url = window.location.href;
      /**************************参数设置*********************************/
      //pt.setParam("appid",1006102);//appid 必须，可以去pt.oa.com申请
      //pt.setParam("daid",6);//did 业务隔离必须使用
      //pt.setParam("s_url","http://id.qq.com");//登录成功跳转url
      //pt.setParam("lang","1033");//登录成功跳转url
      //pt.setParam("style",document.getElementById("style").value);//一般style可以使用20 21 22 23 32 33
      //pt.setParam("protocol","https:");//请求登录框的协议，默认为http:。https的取值为:https:
      //pt.setParam("domain","qq.com");//请求的登录框主域名，默认为qq.com
      //pt.setParam("border_radius",1);//css3的圆角（部分浏览器支持）
      //pt.setParam("target","self"); //跳转方式，支持self跳转和top跳转, 如果是self跳转，可调用父窗口的pt.hidePtui()关闭浮层
      //pt.setParam("maskOpacity",40);//显示登录框的时候，遮罩层的透明度，0-100的数字，0表示不需要mask

      //或者统一设置,把这些参数组成一个object
      pt.setParams({
        "appid":1006102,
        "s_url":url,
        "style":33,
        "protocol":"https:",
        "domain":"qq.com",
        "border_radius":1,
        "target":"self",
        "maskOpacity":0
      });
      /************************ 回调接口设置*****************************/
      //pt.setCallback("resize",function(width,height){console.log(width+" "+height);});
      //当登录框大小改变的时候，业务如果自己处理布局的话，可设置这个接口；对于弹出框的登录框，不建议业务设置
      pt.setCallback("resize", null);
      pt.setParam("maskOpacity", 0);
      pt.setCallback("close", function () {
        console.log('closed!')
      });
      var appendTo=document.getElementById("appendTo");
      pt.showPtui(appendTo);
    },
    Vue.prototype.$loginOut = function () {
      //pt.setCallback("resize", null);
      //pt.showPtui();
      this.$cookies.remove("uin", "/", ".qq.com");
      this.$cookies.remove("skey", "/", ".qq.com");
      this.$router.push("/login");
    }
  }
}



