import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    dialogName: 'none',
    judge_resume:[],
    judge_resume_params:{
      order_by: 1,//1:审核单数默认按审核单数来查
      is_descending: true,
    },
    judge_record:[],
    resumePageConfig:{
      total: null,
      current: 1,
    },
    accountPageConfig:{
      total: null,
      current: 1,
    },
    isReloadData: false,
    is_load: false,
    indexSelectedOpType: null,//代表首页选择的op_type的值，在详情页是取消按钮还是授权按钮
    popTxt: null,//只要显示文字的弹窗的内容
  },
  mutations:{
    changeJudgeResume(state,obj){
      state.judge_resume = obj;
    },
    resumePageTotal(state,total){
      state.resumePageConfig.total = total;
    },
    resumePageCurrent(state,current){
      state.resumePageConfig.current = current;
    },
    accountPageTotal(state,total){
      state.accountPageConfig.total = total;
    },
    accountPageCurrent(state,current){
      state.accountPageConfig.current = current;
    },
    changeDialog(state,name){
      state.dialogName = name;
    },
    timeData(state,data){
      state.timeData = data;
    },
    judge_record(state,data){
      state.judge_record = data;
    },
    judge_resume_params(state,data){
      state.judge_resume_params = data;
    },
    isReloadData(state,data){
      state.isReloadData = data;
    },
    is_load(state,data){
      state.is_load = data;
    },
    indexSelectedOpType(state,data){
      state.indexSelectedOpType = data;
    },
    changePopTxt(state,data){
      state.popTxt = data;
    },
  }
})



