import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const IndexDetails = () => import(/* webpackChunkName: 'IndexDetails' */ '@/views/IndexDetails')
const AccountsDetails = () => import(/* webpackChunkName: 'AccountsDetails' */ '@/views/AccountsDetails')
const LoginPage = () => import(/* webpackChunkName: 'LoginPage' */ '@/views/LoginPage')
const PermissionManage = () => import(/* webpackChunkName: 'PermissionManage' */ '@/views/PermissionManage')

export default new Router({
  routes: [
    {
      path: '/',
      name: 'IndexDetails',
      component: IndexDetails
    },
    {
      path: '/accountsDetails',
      name: 'AccountsDetails',
      component: AccountsDetails
    },
    {
      path: '/login',
      name: 'LoginPage',
      component: LoginPage
    },
    {
      path: '/permissionManage',
      name: 'PermissionManage',
      component: PermissionManage
    }
  ]
})



